<template>
  <div class="container">
    <div class="content">
      <div class="header-section">
        <h1>The Relationship Between Temperature and CO2 Emissions</h1>
      </div>
      <div class="top-section">
        <div class="graph-container">
          <!-- Echart will be inserted here -->
        </div>
        <div class="text-section">
          The relationship between atmospheric temperature and carbon dioxide (CO2) emissions is a cornerstone topic in the study of climate change. It's widely accepted in the scientific community that there is a strong correlation between the two, primarily driven by the greenhouse effect.
          <br/>
          <h4>Explore left chart of the UK's per capita carbon emissions over the last two centuries and an interactive UK map with weather stations. Gain insights into historical carbon footprints and temperature trends, along with a 5-day weather forecast.</h4>
          <ul style="list-style-type: square;">
            <li>Understand the long-term trends in carbon emissions and their impact.</li>
            <li>Access historical  data trend and forecasted weather.</li>
            <li>Engage with climate data through an interactive and user-friendly interface.</li>
          </ul>
          Heating Degree Days(HDD) is computed when daily average temperature is less than 
          18.3°C/65°F. <br/>
          HDD = 18.3° – mean daily temperature to tenths degree Celsius. Each 
          day is summed to produce a monthly total.
        </div>
      </div>
      <div class="map-section">
        <IndMap></IndMap>
      </div>
      <div class="explain">
        This map displays historical data from various weather stations. To view data and trends, click on the weather station icons. For a forecast of the next five days' weather, click the button in the top left corner.
      </div>
    </div>
  </div>
</template>


<script>
import IndMap from "../components/IndMap.vue";
import { csv } from 'd3-fetch';
import * as echarts from 'echarts';

export default {
  name: 'global-map',
  components:{
    IndMap
  },
  mounted() {
    this.loadAndProcessCSV();
  },
  methods: {
    loadAndProcessCSV() {
      csv('./data/GB_sum.csv').then(data => {
        this.displayCO2Data(data);
      });
    },

    displayCO2Data(data) {
      // Process the data to get years and totals
      const processedData = data.map(row => ({
        year: row['Year'],
        total: parseFloat(row['Total'])
      }));

      // Extract years and totals for the chart
      const years = processedData.map(d => d.year);
      const totals = processedData.map(d => d.total);
      // Assuming 'data' is an array of objects with 'Year' and 'Total' properties
      const chartDom = document.querySelector('.graph-container');
      const myChart = echarts.init(chartDom);

      // ECharts option
      const option = {
        title: {
          text: 'CO2 Emissions Over Time in United Kingdom'
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: years
        },
        yAxis: {
          type: 'value',
          axisLabel: {
                formatter: '{value} Tonnes'
          }
        },
        series: [{
          data: totals,
          type: 'line',
          smooth: false
        }]
      };

      // Use the specified configuration item and data to display the chart
      myChart.setOption(option);
    },
  }
};
</script>

<style scoped>
