  display: flex;
  justify-content: center;
  width: 100%;
}

.content {
  display: flex;
  flex-direction: column;
  max-width: 1600px;
  height: 100vh; /* Adjust the height as needed */
  padding: 10px;
}

.header-section {
  text-align: center;
  padding: 20px;
}

.top-section {
  display: flex;
  max-height: 30vh;
  justify-content: space-around;
  color: black;
} 

.text-section {
  width: 25%; /* Adjust width as needed */
  overflow-y: auto; /* Enable vertical scrollbar */
}

.explain {
  position: absolute; /* This positions the footnote within the map-section */
  bottom: 0; /* Aligns the footnote to the bottom of the map-section */
  left: 0; /* Aligns the footnote to the left of the map-section */
  width: 100%; /* Ensures the footnote spans the width of the map-section */
  color: black;
  font-size: smaller; /* Typically footnotes use a smaller font size */
  text-align: center;
  padding: 5px 20px; /* Adjust padding to ensure the text doesn't touch the edges of the map-section */
  background-color: rgba(255, 255, 255, 0.8); /* Optional: Adds a semi-transparent background for readability */
  box-sizing: border-box; /* Ensures padding is included in the width */
  z-index: 10; /* Ensures the footnote is above other content */
}

.graph-container {
  flex-basis: 50%; /* Adjust as needed */
  /* Add padding or margins as needed */
  height: 30vh;
}

.map-section {
  flex-grow: 1; /* Map section takes remaining space */
  overflow-x: hidden; /* Hide horizontal scrollbar */
  border-radius: 10px;
}
</style>