<template>

<div id="map-container">
  <div id="map"></div>

  <ToggleFloatButton :initialVisible="isFloatWindowVisible" @update:visible="updateVisibility" />
  <!-- Floating window overlay -->
  <div class="left-float-window" :class="{ hidden: !isFloatWindowVisible }">
    <div class="container text-center">

      <div class="title">5 Day Weather Forecast</div>
      <div class="text-list">
        <!-- Dynamically generate list items based on weather data -->
        <div v-for="(formattedForecast, index) in forecastGroupedByDate" :key="index" class="date-group">
          <!-- <div class="date-title">{{ formattedForecast.split(' : ')[0] }}</div> -->
          <div class="weather-info">{{ formattedForecast }}</div>
          <img :src="getImageUrl(formattedForecast.split(' : ')[2])" alt="Weather Image" class="weather-image">
          <br/>
        </div>
      </div>

    </div>
  </div>
</div>

</template>

<script>
import mapboxgl from 'mapbox-gl';
import * as d3 from 'd3';
import { createApp } from 'vue';

import { checkDuplicateMarkerCoord } from '../plugins/utils';
import ToggleFloatButton from '../components/tools/ToggleFloatButton.vue';
import D3Chart from "../components/EChart.vue";

let mountedApps = {};

export default {
  name: 'IndMap',

  components:{
    ToggleFloatButton
  },

  data(){
    return{
      map: null, // Map instance
      markers: [],
      addedMarkers: [],

      isFloatWindowVisible: false,

      chartType : 'line',
      dataUrl : './data/uk_climate/UK000000000.csv',
      ColumnNameForX: 'DATE',
      ColumnNameForY: 'TAVG',
      chartWidth: 800,
      chartHeight: 300,

      openWeatherAPI: '51a4229e709d79b1b3f625724a2a8a25',
      forecastGroupedByDate: [], // The forecast weather data
    }
  },

  mounted() {
    this.initializeMap();
    this.updateChartDimensions();
    window.addEventListener('resize', this.updateChartDimensions);
  },
  
  beforeUnmount() {
    // Unmount all Vue app instances when component is unmounted
    Object.keys(mountedApps).forEach(id => {
      if (mountedApps[id]) {
        mountedApps[id].unmount();
      }
    });
  },

  methods: {
    updateVisibility(newValue) {
      this.isFloatWindowVisible = newValue;
    },

    initializeMap() {
      mapboxgl.accessToken = 'pk.eyJ1IjoieDBsZW0iLCJhIjoiY2xyaHA2ejVkMDFsejJrcG43MDA3bjBsaiJ9.Q5hsTi_sqF2QKkZjsII_Ag';

      this.map = new mapboxgl.Map({
        container: 'map', // container id
        style: 'mapbox://styles/mapbox/dark-v10', // style location
        center: [-4.83, 54.42], // starting position [lng, lat]
        zoom: 5.1, // starting zoom
      });

      // Disable scroll zoom
      this.map.scrollZoom.disable()

      // Add zoom and rotation controls to the map.
      this.map.addControl(new mapboxgl.NavigationControl());
      
      // Disable the drag to rotate functionality
      this.map.dragRotate.disable();

      // Disable rotation with touch gestures, but allow pinch to zoom
      this.map.touchZoomRotate.disableRotation();

      this.map.on('load', () => {
        this.setMap(this.map);
      });
    },

    setMap(map){
      this.addBoundaryLayer(map);

      this.addPopupContent("./data/uk_climate/UK000000000.csv");
      this.addPopupContent("./data/uk_climate/UK000003005.csv");
      this.addPopupContent("./data/uk_climate/UK000003026.csv");
      this.addPopupContent("./data/uk_climate/UK000003162.csv");
      this.addPopupContent("./data/uk_climate/UK000003302.csv");
      this.addPopupContent("./data/uk_climate/UK000003377.csv");

      // more UKE
      // this.addPopupContent("./data/uk_climate/UKE/UKE00100808.csv"); // Outside uk
      this.addPopupContent("./data/uk_climate/UKE/UKE00102158.csv");
      // this.addPopupContent("./data/uk_climate/UKE/UKE00105003.csv"); // Error data
      this.addPopupContent("./data/uk_climate/UKE/UKE00105630.csv");
      this.addPopupContent("./data/uk_climate/UKE/UKE00105636.csv");
      this.addPopupContent("./data/uk_climate/UKE/UKE00105642.csv");
      this.addPopupContent("./data/uk_climate/UKE/UKE00105861.csv");
    },

    addBoundaryLayer(map){
      fetch('./data/uk-counties_1682.geojson')
        .then(response => response.json())
        .then(data => {
          map.addSource('gbp-data', {
            type: 'geojson',
            data: data
          });

          map.addLayer({
            id: 'gbp-layer',
            type: 'fill', // This type is for filling areas
            source: 'gbp-data',
            layout: {},
            paint: {
              'fill-color': '#E1FFFF',
              'fill-opacity': 0.6
            }
          });
        })
        .catch(error => console.log(error));
    },

    addPopupContent(path){
      // Read in csv file
      d3.csv(path).then(data => {
        // Data attributes calculation
        const tempMap = data.map(d => +d.TMAX);
        const emntMap = data.map(d=> +d.EMNT);
        const maxTemp = d3.max(tempMap);
        const minTemp = d3.min(emntMap);

        this.markers = data.map(d => {
          // Generate a unique ID for each chart container
          const chartContainerId = `chart-${d.DATE}`;

          return {
            coordinates: [parseFloat(d.LONGITUDE), parseFloat(d.LATITUDE)],
            popupContent: `<div class="popup-content"><h3>All-time low temperature: ${maxTemp} <br/>
            All-time high temperature: ${minTemp}</h3><p>Historical average annual temperature</p><div id="${chartContainerId}"></div></div>`,
            id: d.DATE, // Feature id name
            chartContainerId, // Save this for later use
            dataUrl: path
          };
        });

        // Add anchors and popup      
        this.addMarkers();
      });
    },

    addMarkers() {
      this.markers.forEach(marker => {
        if (this.addedMarkers.length == 0 ||
          !checkDuplicateMarkerCoord(this.addedMarkers, marker.coordinates)) {
          // Create a placeholder div for the popup content
          const popupContentEl = document.createElement('div');
          popupContentEl.className = 'popup-content';
          popupContentEl.innerHTML = marker.popupContent;

          // Add this marker into added array
          this.addedMarkers.push(marker)

          const popup = new mapboxgl.Popup({ maxWidth: "1920px" })
            .setDOMContent(popupContentEl); // Use setDOMContent with the created element

          // change marker icon
          const customIconUrl = './station.png';

          // Create a new HTML element for the custom marker
          const el = document.createElement('div');
          const img = document.createElement('img');
          img.src = customIconUrl;
          img.style.width = '50px'; // Set the size of the marker icon
          img.style.height = '50px';
          el.appendChild(img);

          new mapboxgl.Marker(el)
            .setLngLat(marker.coordinates)
            .setPopup(popup)
            .addTo(this.map);

          popup.on('open', () => {
            this.mountD3Chart(marker.chartContainerId, marker.dataUrl);
          });

          // Add forcast content when marker clicked
          el.addEventListener('click', () => {
            // Logic to handle marker click event
            this.loadForecast(marker.coordinates[1], marker.coordinates[0]);
          });
        }
      });
    },

    mountD3Chart(containerId, dataUrl) {
      const props = {
        chartType: this.chartType,
        dataUrl: dataUrl,
        xColumn: this.ColumnNameForX,
        yColumn: this.ColumnNameForY,
        width: this.chartWidth,
        height: this.chartHeight,
      };
      const container = document.getElementById(containerId);
      if (container) {
        // Check if an app is already mounted and unmount it first
        if (mountedApps[containerId]) {
          mountedApps[containerId].unmount();
        }

        // Mount a new Vue app instance
        // mountedApps[containerId] = createApp(D3Chart, props).mount(container);
        const app = createApp(D3Chart, props);
        mountedApps[containerId] = app;
        app.mount(container);
      }
    },

    updateChartDimensions() {
      // Simple example, adjust as needed for your design
      this.chartWidth = window.innerWidth/2.5;
      this.chartHeight = window.innerHeight/4;
    },

    convertTimestampToDate(timestamp) {
      const date = new Date(timestamp * 1000); // Convert to milliseconds
      return date.toLocaleDateString('en-GB', { day: '2-digit', month: '2-digit', year: '2-digit' });
    },

    groupForecastByDate(forecast) {
      const grouped = {};
      forecast.forEach(item => {
        const date = this.convertTimestampToDate(item.dt); // Assuming dt is in seconds
        // Ensure only the first entry for each date is used
        if (!grouped[date]) {
          grouped[date] = item;
        }
      });
      return grouped;
    },

    formatGroupedForecast(groupedForecast) {
      const formatted = [];
      Object.keys(groupedForecast).forEach(date => {
        const item = groupedForecast[date];
        const feelsLike = item.main.feels_like.toFixed(2) + '°C';
        const description = item.weather[0].description;
        formatted.push(`${date} : ${feelsLike} : ${description}`);
      });
      return formatted;
    },

    loadForecast(lat, lon) {
      const openWeatherURL = `https://api.openweathermap.org/data/2.5/forecast?lat=${lat}&lon=${lon}&appid=${this.openWeatherAPI}&units=metric`;

      fetch(openWeatherURL)
        .then(response => {
          if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
          }
          return response.json(); // Parse the JSON of the response
        })
        .then(data => {
          // Save the fetched data
          const groupedForecast = this.groupForecastByDate(data.list);
          this.forecastGroupedByDate = this.formatGroupedForecast(groupedForecast);
        })
        .catch(error => {
          console.error("Failed to fetch weather data:", error);
          // Handle errors here
        });
    },

    getImageUrl(description) {
      const mapping = {
        'scattered ': './weather_icons/sun_rain.png',
        'light rain': './weather_icons/rain.png',
        'overcast clouds': './weather_icons/going_rain.png',
        'clear sky': './weather_icons/sun.png',
        'few clouds': './weather_icons/few_clouds.png'
      };
      return mapping[description] || './weather_icons/going_rain.png'; // Fallback image
    },

  }
}

</script>

<style scoped>
#map-container {
  position: relative;
  width: 100%;
  margin-bottom: 20px; /* Space below the map container */
}

#map {
  width: 100%;
  height: 59vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.popup-content {
  min-width: 400px; /* Minimum width */
  padding: 15px; /* Add some padding */
  box-sizing: border-box; /* Include padding in width calculation */
  background-color: rgb(34, 9, 39);
  border-radius: 15px; /* Rounded corners */
}

/* Button */

.left-float-window.hidden {
  display: none;
}

.left-float-window .title{
  text-align: right;
  color: black;
}

/*Float window used css*/
.left-float-window {
  position: absolute; /* or 'absolute' depending on your layout */
  top: 20px; /* Adjust as needed */
  left: 20px; /* Adjust as needed */
  width: 25%; /* Adjust based on content */
  background-color: white;
  border: 1px solid #ccc;
  z-index: 10; /* Ensure it floats above other content */
  overflow-y: auto; /* Make the floating window scrollable */
  text-align: center;
  max-height: 55vh;
  color: darkturquoise;
}

.left-float-window .weather-image {
  max-width: 10%; /* Ensures image does not exceed the container width */
  height: 10%; /* Maintains aspect ratio */
  border-radius: 5px; /* Optional: adds rounded corners to the images */
  margin: 10px; /* Optional: adds some space around the image */
}

.title {
  padding: 10px;
  font-size: 1.2em;
  font-weight: bold;
  border-bottom: 1px solid #ccc;
}

.text-list {
  flex: 1;
  padding: 10px;
  overflow-y: auto;
}

.text-item {
  margin-bottom: 5px;
}

</style>
